"use client"
import Link from "next/link";
import { ArrowRight, LogIn, BarChart3, Users, Server, Quote } from "lucide-react";

// --- Seção Principal (Hero) ---
function HeroSection() {
  return (
    <section className="bg-white">
      <div className="relative z-10 flex flex-col items-center justify-center p-6 text-center pt-24 pb-20 md:pt-32 md:pb-28">
        
        {/*
          // LOGO VAI AQUI
          // 1. Coloque seu arquivo de logo (ex: logo-foodflow.png) na pasta 'public' na raiz do projeto.
          // 2. Descomente e use o código abaixo. Lembre-se de importar o 'Image' do 'next/image'.
          
          <div className="mb-6">
            <Image 
              src="/logo-foodflow.png"
              alt="Logo FoodFlow"
              width={80}
              height={80}
              priority
            />
          </div>
        */}

        <h1 className="text-5xl md:text-6xl font-bold bg-gradient-to-br from-black to-slate-600 bg-clip-text text-transparent max-w-3xl">
          O controle de estoque, finalmente simples.
        </h1>
        <p className="mt-4 text-lg text-slate-600 max-w-xl">
          A plataforma inteligente para gerenciar seus inventários sem complicações.
        </p>
        <div className="mt-10 flex flex-col sm:flex-row items-center gap-4">
          <Link 
            href="/register" 
            className="inline-flex items-center justify-center gap-2 px-8 py-3 rounded-lg bg-green-600 text-white font-bold hover:bg-green-700 transition-all shadow-lg shadow-green-500/30 hover:shadow-green-500/40 text-lg w-full sm:w-auto"
          >
            Criar Conta Gratuitamente
            <ArrowRight size={20} />
          </Link>
          <Link 
            href="/login"
            className="inline-flex items-center justify-center gap-2 px-8 py-3 rounded-lg text-slate-700 font-semibold hover:bg-slate-200 transition-colors w-full sm:w-auto"
          >
            <LogIn size={18} />
            Fazer Login
          </Link>
        </div>
      </div>
    </section>
  );
}

// --- Seção de Funcionalidades ---
function FeaturesSection() {
    const features = [
        {
            icon: <Server size={24} className="text-green-600"/>,
            title: "Inventário Centralizado",
            description: "Visão completa de múltiplos locais em um único dashboard, com dados sincronizados."
        },
        {
            icon: <Users size={24} className="text-green-600"/>,
            title: "Gestão de Equipes",
            description: "Controle permissões de acesso e atribua responsabilidades para cada membro do time."
        },
        {
            icon: <BarChart3 size={24} className="text-green-600"/>,
            title: "Relatórios Inteligentes",
            description: "Tome decisões baseadas em dados com relatórios de consumo, validade e tendências."
        }
    ];

    return (
        <section className="bg-slate-50 py-20 border-y border-slate-200">
            <div className="container mx-auto px-6">
                <div className="text-center max-w-3xl mx-auto">
                    <h2 className="text-3xl font-bold text-slate-900">Controle Total em um Só Lugar</h2>
                    <p className="mt-3 text-lg text-slate-600">As ferramentas que você precisa para transformar dados em decisões inteligentes.</p>
                </div>
                
                <div className="mt-16 grid grid-cols-1 md:grid-cols-3 gap-8">
                    {features.map(feature => (
                        <div key={feature.title} className="p-8 text-left bg-white rounded-2xl border border-slate-200 shadow-md hover:shadow-2xl hover:border-green-300 transition-all duration-300 transform hover:-translate-y-2">
                            <div className="inline-block p-3 bg-green-100 rounded-xl">
                                {feature.icon}
                            </div>
                            <h3 className="mt-5 text-xl font-semibold text-slate-900">{feature.title}</h3>
                            <p className="mt-2 text-slate-600">{feature.description}</p>
                            <a href="#" className="mt-4 font-semibold text-green-600 inline-flex items-center gap-1 group">
                                Saiba mais
                                <ArrowRight size={16} className="transition-transform group-hover:translate-x-1" />
                            </a>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}

// --- Seção de Depoimento ---
function TestimonialSection() {
    return (
        <section className="bg-white py-20">
            <div className="container mx-auto px-6 max-w-4xl text-center">
                <Quote className="text-green-200 mx-auto" size={48} />
                <blockquote className="mt-4 text-2xl font-light text-slate-800 italic">
                    "Finalmente temos uma visão clara de todo o nosso inventário em um só lugar. O FoodFlow é intuitivo, poderoso e se tornou indispensável para a nossa operação."
                </blockquote>
                <cite className="mt-6 block font-semibold text-slate-600 not-italic">
                    - Carla Martins, Diretora de Operações
                </cite>
            </div>
        </section>
    );
}


// --- Componente Principal da Página ---
export default function Home() {
  return (
    <main className="flex-grow">
      <HeroSection />
      <FeaturesSection />
      <TestimonialSection />
    </main>
  )
}